<template>
  <div class="container">
    <h1 id="headline">Countdown to off duty</h1>
    <div id="countdown">
      <h1>Ë∑ùÁ¶ª‰∏ãÁè≠ËøòÊúâ</h1>
      <ul>
        <li><span id="hours"></span></li>
        <li><span>:</span></li>
        <li><span id="minutes"></span></li>
        <li><span>:</span></li>
        <li><span id="seconds"></span></li>
      </ul>
    </div>
    <div id="content" class="emoji">
      <span>ü•≥</span>
      <span>‰∏ãÁè≠Âï¶</span>
      <span>üéâ</span>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "countDown",
  components: {},
  data() {
    return {};
  },
  methods: {
    fnCountDown() {
      let today = new Date();
      let countDown = new Date(`${today.toDateString()} 19:00:00`).getTime();

      const second = 1000;
      const minute = second * 60;
      const hour = minute * 60;
      const day = hour * 24;

      // ËÆ°Êó∂Âô®
      const x = setInterval(() => {
        const now = new Date().getTime();
        const distance = countDown - now;
        // document.getElementById("days").innerText = Math.floor(distance / day);
        document.getElementById("hours").innerText = this.getZero(
          Math.floor((distance % day) / hour)
        );
        document.getElementById("minutes").innerText = this.getZero(
          Math.floor((distance % hour) / minute)
        );
        document.getElementById("seconds").innerText = this.getZero(
          Math.floor((distance % minute) / second)
        );

        // Êó∂Èó¥Âêé
        if (distance < 0) {
          document.getElementById("headline").innerText =
            "It's time to get off work!";
          document.getElementById("countdown").style.display = "none";
          document.getElementById("content").style.display = "block";
          clearInterval(x);
        }
        // seconds
      }, 1000);
    },
    getZero(n) {
      return n > 9 ? n : "0" + n;
    },
    async init() {
      const { data: res } = await axios.get(
        "https://timor.tech/api/holiday/info/" + "2022-09-09"
      );
      console.log(res);
      if (res.code !== 0) {
        this.$message.error("Ëé∑ÂèñÂ§±Ë¥•,ËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñËÄÖËÅîÁ≥ªÂºÄÂèëÔºÅ");
      }
      if (res.type.type != 0) {
        this.$message.error("‰ºëÊÅØÊó•ÔºÅ");
        document.getElementById("headline").innerText = "‰ªäÂ§©‰∏ä‰∏™È¨ºÁè≠!";
      } else {
        // Â¶ÇÊûúÊòØÂ∑•‰ΩúÊó•
        this.$message.success("Â∑•‰ΩúÊó•ÔºÅ");
        this.fnCountDown();
      }
    },
  },
  created() {
    this.init();
    this.fnCountDown();
  },
};
</script>

